@media #{$small-up}{

  // Hero
  #hero{
    position: relative;
    display: flex;
    flex-direction: column;
  }

  #hero-photo{
    background: $blue;
    position: relative;
    height: 70vw;
    animation: 1s ease-out reveal;

    // Hide profile photo on mobile
    > img{
      display: none;
    }
  }

  #hero-logo{
    position: absolute;
    top: 14vw;
    left: 15%;
    width: 70%;
    animation: 1.5s ease-out reveal;
  }

  #hero-content{
    padding: 2.5rem 1.5rem;
    display: flex;
    align-items: center;
    text-align: center;
    animation: 2.5s ease-out reveal;
    @include link-round($blue, $white);

    p{
      text-align: left;
      padding: 0;

      &:first-child{
        margin: 0 0 2rem;
      }

      &:last-child{
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }

    .field--name-body{
      padding: 0;
    }
  }
}

@media #{$medium-up}{

  // Hero
  #hero{
    flex-direction: row;
  }

  #hero-photo{
    height: auto;
    flex-basis: 61.25%;
    background: transparent;
    animation: 1s ease-out fromLeft;

    // Reveal profile photo
    > img{
      display: block;
    }
  }

  #hero-logo{
    position: absolute;
    top: 15vw;
    left: 8.5vw;
    width: 48%;
    animation: 1.5s ease-out fromLeft;
  }

  #hero-content{
    margin-left: -2vw;
    flex-basis: 38.75%;
    text-align: left;
    padding: 3.5vw 4vw 4vw 0;
    background: transparent;
    animation: 2.5s ease-out reveal;

    p{
      font-size: 1.4vw;
      text-align: justify;

      &:first-child{
        margin: 3vw 0 2vw;
      }
    }
  }
}

@media #{$mobile-landscape}{

  // Hero
  #hero{
    position: relative;
    display: flex;
    flex-direction: column;
  }

  #hero-photo{
    background: $blue;
    position: relative;
    height: 70vh;
    animation: 1s ease-out reveal;

    // Hide profile photo on mobile
    > img{
      display: none;
    }
  }

  #hero-logo{
    position: absolute;
    top: 10vh;
    left: 10vh;
    height: 50vh;
    animation: 1.5s ease-out reveal;
  }

  #hero-content{
    padding: 2.5rem 1.5rem;
    display: flex;
    align-items: center;
    text-align: center;
    animation: 2.5s ease-out reveal;
    @include link-round($blue, $white);

    p{
      font-size: 1.5rem;
      text-align: center;
      padding: 0;

      &:first-child{
        margin: 0 1rem 2rem;
      }

      &:last-child{
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }

    .field--name-body{
      padding: 0;
    }
  }
}
